body,
html {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}
/* General styles */
body {
	font-family: Arial, sans-serif;
	margin: 0;
	padding: 0;
	color: #333;
}

/* Header */
header.header {
	background-color: #d4e7c5;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 2rem 2rem;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

header.header h1.header__title {
	font-size: 2rem;
	margin: 0;
}

header.header a.header__menu {
	display: block;
	font-size: 1.5rem;
	cursor: pointer;
	color: #333;
	align-content: center;

	padding-inline: 10px;
	padding-block: 10px;
}
nav.nav {
	display: flex;
	flex-direction: row;
}
nav.nav ul.nav__list {
	display: flex;
	flex-direction: row;
	gap: 20px 20px;
}
nav.nav ul.nav__list li.nav__item {
	list-style-type: none;
}
nav.nav ul.nav__list li.nav__item a {
	text-decoration: none;
	color: #616161;

	align-content: center;
}

nav.nav ul.nav__list li.nav__item a:hover {
	text-decoration: underline;
	color: #000;
}

#drawer.active {
	display: block;
}

/* Hero */
div.hero {
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
	background-position: center;
	width: 100%;
	min-height: 400px;

	display: flex;
	filter: drop-shadow(0px 2px 100px #e1f0da);
}

/* Main */
main {
	padding: 2rem;
	background-color: #99bc85;
}
div.content {
	padding: 32px;
}
div.content h1.content__label {
	font-size: 32px;
	font-weight: lighter;
	text-align: center;
}
.card-list {
	margin-block: 1rem;

	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));

	column-gap: 2%;
	row-gap: 24px;
}

/* Footer */
footer {
	background-color: #f5f5f5;
	text-align: center;
	padding: 1rem;
}

/*dimensi aksesibilitas  */
a,
input,
button,
textarea {
	display: inline-block;
	min-width: 44px;
	min-height: 44px;
}

/* 
	restaurant details 
*/

.detail-container {
	display: grid;
	gap: 20px;
	background-color: #fff; 
	border-radius: 10px; 
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
	width: 100%; 
	margin: 15px auto; 
	padding: 20px; 
	color: #333;
  }
  
  /* Restaurant title */
  .restaurant__title {
	font-size: 24px; 
	font-weight: bold; 
	margin-bottom: 10px; 
  }
  
  /* Restaurant poster image */
  .restaurant__poster {
	width: 100%; 
	max-width: 500px;
	/* Adjust image height */
	object-fit: cover; 
  }
  
  /* Restaurant info section */
  .restaurant__info {
	margin-top: 15px;
  }
  
  /* Information headings */
  .restaurant__info h3,
  .restaurant__info h4, 
  .restaurant__menu h3,
  .restaurant__menu h4,
  .restaurant__overview h3{
	font-size: 18px; 
	margin-bottom: 5px; 
  }
  
  /* Information text */
  .restaurant__info p {
	margin-bottom: 10px; 
  }
  
  /* Menu list styling */
  .restaurant__info ul {
	list-style: none; 
	padding: 0; 
	margin: 0; 
  }
  
  /* Menu list items */
  .restaurant__info ul li {
	margin-bottom: 5px; 
  }
  
  /* Restaurant overview section */
  .restaurant__overview {
	margin-top: 15px;
;
  }

  .restaurant__overview div.review__container{
	background-color: #E1F0DA; 
	border-radius: 10px; 
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
	width: 100%; 
	margin: 15px auto; 
	padding: 20px; 
  }
  
  /* Overview text */
  .restaurant__overview p {
	font-style: italic; 
  }
  
/*
  Like
*/

.like {
	width: 55px;
	height: 55px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #db0000;
	
	position: fixed;
	bottom: 16px;
	right: 16px;
	border-radius: 50%;
	border: 0;
	
	font-size: 18px;
	color: white;
	cursor: pointer;
  }

/* 
Tambahan styling
*/
   
.restaurant__title a{
	text-decoration: none;
	color: #616161;
}
